
* {
    padding: 0;
    margin: 0;
    box-sizing: border-box; }

.hide {
    display: none; }

.speaker-icon {
    width: 50px;
    position: absolute;
    right: 0;
    top: 0;
    margin: 20px; }

.speaker-icon:hover {
    cursor: pointer; }

