//GAME-CONTAINER

.game-container {
    height: 80vh;
    display: flex;
    align-content:center {}
    justify-content: center;
    align-items: center; }

.memory-game {
  width: 75vw;
  height: 75vh;
  margin: auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-content: center;
  perspective: 1000px; }


.memory-card {
  justify-content:center {}
  height: 25vh;
  margin: 5px;
  position: relative;
  transform: scale(1);
  transform-style: preserve-3d;
  transition: transform .5s; }


.memory-card:active {
  transform: scale(0.97);
  transition: transform .2s; }


.memory-card.flip {
  transform: rotateY(180deg); }

.front-face {
  width: 100%;
  height: 100%;
  position: absolute;
  border-radius: 40px;
  backface-visibility: hidden; }

.back-face {
  width: 100%;
  height: 100%;
  position: absolute;
  border-radius: 40px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: bottom;
  backface-visibility: hidden; }


.front-face {
  transform: rotateY(180deg); }

#info {
    height: 70vh; }

#start {
    height: 66.3vh; }

#finished {
    height: 70vh; }

.infoimg {
    height: 100%;
    width: auto; }

.leveltext:hover {
    stroke-width: 2px;
    stroke: red; }

.playAgain-container {
    height: 70vh; }

#playagain {
    height: 70vh; }

.playagain {
  height: 70vh; }
