.tour__content {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;
    .content__image {
        z-index: 100;
        overflow: hidden;
        margin-top: 50px;
        width: $sr-width * 2.6px;
        height:  $sr-height * 2.6px;
        background-color: white;
        border-radius: $br-main;
        border: 6px solid $second-color; } }



// .pnlm-sprite
